import React from 'react';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Pie } from 'react-chartjs-2';

ChartJS.register(ArcElement, Tooltip, Legend);



const PieChart = ({ dadaHomes, dadaDones, titol }) => {

    const data = {
        labels: ['Homes', 'Dones'],
        datasets: [
            {
                label: '# of Votes',
                data: [dadaHomes, dadaDones],
                backgroundColor: [
                    'rgb(75, 192, 192, 0.5)',
                    'rgb(255, 99, 132, 0.5)',
                ],
                borderColor: [
                    'rgb(75, 192, 192)',
                    'rgb(255, 99, 132)',
                ],
                borderWidth: 1,
            },
        ],
    };

    const options = {
        plugins: {
            title: {
              display: true,
              text: titol,
            },
          },
        maintainAspectRatio: false
    }


    return (

        <Pie 
            data={data}
            options={options}  
            width={"30%"} 
        />

    )
}

export default PieChart;