import { Offcanvas } from 'react-bootstrap';

const Sidebar = ({ show, handleClose }) => {
    return (
        <Offcanvas show={show} onHide={handleClose}>
        <Offcanvas.Header closeButton>
            <Offcanvas.Title>Informació</Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body>
            Dades sobre la participació de la dona al Moros i Cristians d'Alcoi extretes a partir de l'informació accessible de l'Associació de Sant Jordi https://www.asjordi.org/.
        </Offcanvas.Body>
    </Offcanvas>
    )
}

export default Sidebar;