import { NavDropdown } from 'react-bootstrap';

const Dropdown = ({dadesEvolucio}) => {

    let anysList = Object.keys(dadesEvolucio).reverse();

    const setYears = (item) => { 
        return <NavDropdown.Item eventKey={item}>{item}</NavDropdown.Item>
    }

    return (
        <div>
        {(anysList !== undefined ? anysList.map(setYears):"")}
        </div>
    )
}

export default Dropdown;