import React from 'react';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
import { Line } from 'react-chartjs-2';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

const options = {
  plugins: {
    title: {
      display: true,
      text: "Evolució d'associats per génere",
    },
  },
  responsive: true,
  // interaction: {
  //   mode: 'index',
  //   intersect: false,
  // },
  // scales: {
  //   x: {
  //     stacked: true,
  //   },
  //   y: {
  //     stacked: true,
  //   },
  // },
  // maintainAspectRatio: false
};



const BarChart = ({ dadesEvolucio }) => {

  const labels = Object.keys((dadesEvolucio === undefined ? "" : dadesEvolucio));

  let dadesEvolucioHomes = [];
  let dadesEvolucioDones = [];

  for (let i=0; i<labels.length; i++){
    dadesEvolucioHomes.push(dadesEvolucio[labels[i]].homes)
    dadesEvolucioDones.push(dadesEvolucio[labels[i]].dones)
  }

  const data = {
    labels,
    datasets: [
      {
        label: 'Homes',
        data: dadesEvolucioHomes,
        backgroundColor: 'rgb(75, 192, 192, 0.5)',
        borderColor:'rgb(75, 192, 192, 0.7)'

      },
      {
        label: 'Dones',
        data: dadesEvolucioDones,
        backgroundColor: 'rgb(255, 99, 132, 0.5)',
        borderColor: 'rgb(255, 99, 132, 0.7)'

      },
    ],
  };

  return (

    <Line 
      options={options} 
      data={data} 
      // width={"30%"} 
      height={"70%"}
    />

  )
}

export default BarChart;